.swiper-container {
  margin-bottom: 60px;
}

.course-card {
  border-radius: 20px;
  overflow: hidden;
  cursor: pointer;
  max-width: 400px;
  @media (max-width: 400px) {
    max-width: unset;
  }
  .show-img-p {
    overflow: hidden;
    img {
      transition: $transition-all-slow;
    }
  }

  .clock-icon {
    width: 16px;
    height: 16px;
  }

  .text {
    font-size: 18px;
  }

  &:hover {
    .show-img-p {
      img {
        transform: scale(1.06);
      }
    }
    .title-sm {
      opacity: 0.7;
    }
  }
}

.courses-swiper-section {
  padding-top: 80px;
  padding-bottom: 80px;
  @include media-breakpoint-down(xs) {
    padding-top: 40px;
    padding-bottom: 60px;
  }
  .container {
    @media (max-width: 1394px) {
      padding: 0 78px;
    }
    @include media-breakpoint-down(md) {
      padding: 0 84px;
    }
    @include media-breakpoint-down(xs) {
      padding: 0 15px;
    }
  }

  .swiper-button-next,
  .swiper-button-prev {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background-color: $light-sheer;
    z-index: 3;
    @include media-breakpoint-down(xs) {
      width: 32px;
      height: 32px;
      top: unset;
      bottom: -48px;
    }

    &:after {
      content: "";
      background-image: url("../../assets/img/arrow-right.svg");
      background-size: contain;
      width: 14px;
      height: 14px;
      @include media-breakpoint-down(xs) {
        width: 10px;
        height: 10px;
      }
    }
  }
  .swiper-button-next {
    right: -72px;
    @media (max-width: 1433px) {
      right: -70px;
    }
    @media (max-width: 1394px) {
      right: 0;
    }
    @include media-breakpoint-down(md) {
      right: 10px;
    }
    @include media-breakpoint-down(xs) {
      right: 50%;
      transform: translateX(calc(100% + 10px));
    }
  }

  .swiper-button-prev {
    left: -72px;
    &:after {
      transform: rotate(-180deg);
    }
    @media (max-width: 1433px) {
      left: -70px;
    }
    @media (max-width: 1394px) {
      left: 0;
    }
    @include media-breakpoint-down(md) {
      left: 10px;
    }
    @include media-breakpoint-down(xs) {
      left: 50%;
      transform: translateX(calc(-100% - 10px));
    }
  }
}
