.vedio-icon {
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
}

.dot-nav-section {
  height: 260px;
  min-width: 1150px;
  .bg-gd {
    // width: 7%;
    border-radius: 10px;
    left: 0;
    background: linear-gradient(90deg, #f92204, #7f2630);
    transition: 0.5s;
  }
}
.opacity-d {
  width: 93%;
  position: absolute;
  right: 0;
  height: 100%;
  background: rgba(255, 255, 255, 0.6);
  z-index: 9;
  transition: 0.5s;
}
.dot-nav {
  height: 4px;
  background: $light;
  border-radius: 10px;
  max-width: 1020px;

  .nav-dot {
    display: inline-block;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
  }
  .nav-text {
    width: 160px;
    left: 12px;
    position: absolute;
    bottom: 12px;
    height: 110px;
    border-left: 1px dotted #333;
    padding-left: 10px;
    z-index: -1;
  }
  .nav-item {
    position: relative;
    .item-time {
      color: $red;
    }
    &.item-black {
      .item-time {
        color: $dark-blue;
      }
      .nav-dot {
        background-color: $dark-blue;
        border-color: $dark-blue;
        &::after {
          background: #fff;
        }
      }
    }
    &:nth-child(1) {
      width: 8%;
      .nav-text {
        width: 70px;
      }
    }
    &:nth-child(2) {
      width: 5%;
    }
    &:nth-child(3) {
      width: 20%;
    }
    &:nth-child(4) {
      width: 10%;
    }
    &:nth-child(5) {
      width: 27.5%;
    }
    &:nth-child(6) {
      width: 27.5%;
    }
    &:nth-child(2),
    &:nth-child(5) {
      .nav-text {
        display: flex;
        align-items: flex-end;
        bottom: -108px;
      }
    }
    &.active {
      .nav-text {
        z-index: 9;
      }
      .nav-dot {
        z-index: 99;
      }
    }
  }
}
