.questions-swiper-wrap {
    .custom-swiper-nav {
      position: absolute;
      top: 23px;
      display: flex;
      justify-content: space-between;
      gap: 5px;
      width: 100%;
      z-index: 55;
      padding: 0 22px;
  
      .swiper-nav-section {
        border-radius: 2px;
        height: 3px;
        flex: 1;
        background-color: $extra-sheer;
        position: relative;
        overflow: hidden;
        &.filled {
          background-color: $white;
        }
        .progress-loader{
          position: absolute;
          left: 0;
          top: 0;
          bottom: 0;
          background-color: $white;
          border-radius: 2px;
          height: 3px;
          //transition: all 0.3s linear;
        }
      }
    }
    .video-container {
      height: 490px;
      padding-bottom: 0;
      border-radius: 20px;
      position: relative;
      overflow: hidden;
  
      &:after {
        content: "";
        position: absolute;
        background: linear-gradient(180deg, rgba(0,0,0,0.3) 0%, rgba(0,0,0,0) 100%);
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 3;
      }
  
  
      @include media-breakpoint-down(lg){
        height: 380px;
      }
      .video-play {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 82px;
        height: 82px;
        transform: translate(-50%, -50%);
        z-index: 33;
        cursor: pointer;
        &.hidden {
          display: none;
        }
      }
      &.playing {
        &:after {
          content: none;
        }
      }
    }
    .swiper-pagination {
      position: relative;
      top: unset;
      bottom: unset;
      text-align: start;
    }
    .swiper-pagination-bullet {
      display: flex!important;
      height: 100%;
      align-self: stretch;
      opacity: 1;
      padding: 30px;
      width: 100%;
      margin-left: 0;
      background-color: $white!important;
      border-radius: 10px;
      @include media-breakpoint-down(xs) {
        padding: 20px;
      }
      img, p {
        transition: $transition-all;
      }
      &.swiper-pagination-bullet-active, &:hover {
        -webkit-box-shadow: 0px 3px 10px 0px rgba(0,0,0,0.05);
        box-shadow: 0px 3px 10px 0px rgba(0,0,0,0.05);
        p {
          color: $red;
        }
        img {
          transform: rotate(-90deg);
        }
      }
    }
    .swiper-button-prev, .swiper-button-next {
      top: 245px;
      @include media-breakpoint-down(lg){
          top: 190px;
      }
    }
    .swiper-button-prev {
      @media (max-width: 1433px) {
        left: -74px;
      }
      @media (max-width: 1394px) {
        left: -74px;
      }
      @include media-breakpoint-down(sm){
        left: -44px;
      }
      @include media-breakpoint-down(xs){
        top: 410px;
        left: 15px;
        transform: none;
      }
    }
    .swiper-button-next {
      @media (max-width: 1433px) {
        right: -74px;
      }
      @media (max-width: 1394px) {
        right: -74px;
      }
      @include media-breakpoint-down(sm){
        right: -44px;
      }
      @include media-breakpoint-down(xs){
        top: 410px;
        right: 15px;
        transform: none;
      }
    }
  
  }
  