.form-group {
  margin-bottom: 10px;
}
.form-control {
  background: $dark-light;
  font-size: 16px;
  height: 56px;
  border-radius: 10px;
  border-color: $dark-light;
  &:focus {
    box-shadow: none;
    background: $dark-light;
    border-color: $dark-light;
  }
}
textarea {
  &.form-control {
      padding: 24px 20px;
    resize: none;
    height: 160px;
  }
}
