body {
  &.fixed {
    position: fixed;
  }
}

html {
  font-size: 20px;
  scroll-behavior: smooth;

  //background-color: $black;
}

* {
  font-family: "Sen", sans-serif;
  font-weight: 400;
}

img {
  height: auto;
}

a,
button {
  transition: 0.3s;
}

a {
  color: inherit;

  @include hover-focus-active {
    //color: inherit;
    //text-decoration: none;
  }
}

[type="button"],
[type="reset"],
[type="submit"],
button {
  -webkit-appearance: initial;
}

.padding-top-block {
  padding-top: 160px !important;
}

.transition {
  transition: 0.3s;
}
.bg-light,
.g-top {
  background: $light;
}
.radius-20 {
  border-radius: 20px;
}
.radius-10 {
  border-radius: 10px;
}
.overflow-hidden {
  overflow: hidden;
}
.helvetica {
  font-family: $helvetica;
}
.cursor-pointer {
  cursor: pointer;
}
.scale-hover {
  transform: scale(1);
  transition: all 0.6s ease-in-out;
  &:hover {
    transform: scale(1.15);
  }
}
.color-dark-gray {
  color: $dark-gray;
  opacity: 0.8;
}
.overflow-x-auto {
  overflow-x: auto;
  &::-webkit-scrollbar {
    width: 10px;
  }
  &::-webkit-scrollbar:horizontal {
    height: 10px;
  }
  &::-webkit-scrollbar-track {
    background-color: transparentize(#ccc, 0.7);
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: transparentize(#ccc, 0.5);
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
  }
}

.img-square {
  position: relative;
  &::after {
      content: "";
      display: block;
      padding-bottom: 109%;
  }
  img {
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
  }
}

.swiper-button-next,
.swiper-button-prev {
  background-color: rgba(0,41,73,0.05) !important;
  &:hover {
    transition-duration: .2s;
    background-color: rgba(0,41,73,0.09) !important;
  }
}