.questions-link-block {
    * {
      color: $white;
    }
    .row {
      background-color: $dark-blue;
      border-radius: 10px;
      padding: 50px 45px 50px 60px;
      @media (max-width: 460px) {
        padding: 50px 40px 30px 40px;
      }
    }
  
  .link-circle-arr {
    &:after {
      transition: $transition-all;
    }
    &:hover {
      &:after {
        transform: translate(-50%, -50%) scale(1.2);
      }
    }
  }
  
}