//remove default styling
button {
  box-shadow: none;
  outline: none;
  background-color: transparent;
  border: none;
}
.button {
  padding: 18px 30px;
  border-radius: 28px;
  position: relative;
  font-size: 16px;
  font-weight: 700;
  line-height: 19px;
  line-height: normal;
  transition: $transition-all;
  color: $white;
  @include media-breakpoint-down(xs){
    font-size: 14px;
    line-height: 17px;
    padding: 16px 24px;
  }
  &.transparent-button {
    border: $white-border;
    &:hover {
      background-color: rgba(225, 225, 225, 0.1);
      border: $white-border-hover;
    }
  }
  &.red-btn {
    // border: 1px solid $red;
    background-color: $red;
    &:hover {
      background-color: transparent;
      color: $red;
    }
  }

  &.grey-btn {
    // // border: 1px solid $light-gray;
    background-color: $light-gray;
    &:hover {
      background-color: transparent;
      color: $light-gray;
    }
  }
}

.hover-for-links {
  transition: color 0.3s ease-in-out;
  position: relative;
  &::before {
    content: "";
    position: absolute;
    bottom: -18px;
    left: 0;
    right: 0;
    height: 1px;
    background-color: $red;
    transform: scaleX(0);
    transition: transform 0.4s ease-in-out;
    transform-origin: right;
  }
  &:hover, &:focus, &.active-link {
    //color: $blue;
    text-decoration: none;
    &::before {
      transform: scaleX(1);
      transform-origin: left;
    }
  }
  &.hover-for-links-white {
    &::before {
      // background-color: $white;
    }
  }
}

.button3d {
    width: 60px;
    height: 60px;
    background-color: lighten(#282626, 5);
  box-shadow: inset -2px -2px 5px rgba(0, 0, 0, 1),
  inset 3px 3px 5px rgba(0, 0, 0, 0.1);
    //border-radius: 10px;
    margin-bottom: 10px;
  transition: all 0.2s;
  img {
    width: 20px;
    height: 20px;
    opacity: 0.8;
    transition: all 0.3s;
  }

  &:hover {
    background-color: lighten(#282626, 7);
    img {
      opacity: 0.9;
    }
  }

  &:active {
    box-shadow: -2px -2px 5px rgba(0, 0, 0, 1),
    3px 3px 5px rgba(0, 0, 0, 0.1);
    background-color: darken(#282626, 2);

      img {
        transform: scale(0.90);
        opacity: 0.7;
      }

  }
  }

.app-links {
  a {

    transition: $transition-all;
    &:hover {
      opacity: 0.8;
      transform: scale(0.98);
    }
  }
}

.read-more-link {
  margin-left: 15px;
  img {
    transition: $transition-all;
    margin-left: -15px;
  }
  span {
    margin-left: 15px;
    transition: $transition-all;
  }
  &.red {

    &:hover {
      span {
        color: $red!important;
        margin-left: 7px;
        //text-decoration: underline;

      }
      img {
        margin-left: 0!important;
      }
    }

  }
}

.btn-round {
  box-sizing: border-box;
  height: 48px;
  width: 48px;
  border-radius: 24px;
  background-color: $dark-light;
  color: rgba(0, 41, 73, 0.4);
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn-round-item .text {
  opacity: 0.8;
}

.btn-round-item:hover {
  .btn-round {
    background-color: $red;
    color: #fff;
  }
  .text {
    opacity: 1;
  }
} 