
.img-wrap-drop-round {
  border-radius: 0px 50% 50% 50%;
  overflow: hidden;
  max-width: 386px;
}
.img-wrap-drop-elongated {
  border-radius: 0px 50% 40% 40%;
  overflow: hidden;
  max-width: 488px;
}
.img-wrap-drop-round-lg {
  border-radius: 0px 50% 50% 50%;
  overflow: hidden;
  max-width: 590px;
}
.show-img-pp {
  position: relative;
  padding-bottom: 100%;
  &.show-img-pp-80 {
    padding-bottom: 80%;
  }
  &.show-img-pp-65 {
    padding-bottom: 65%;
  }
  .show-img-p {
    position: absolute;
    width: 100%;
    height: 100%;
  }
}
.cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.section-img-wrap {
  width: fit-content;
}