.social-icons {
  .social-icon-wrap {
    width: 50px;
    height: 50px;
    border: 1px solid $extra-sheer;
    border-radius: 50%;
    display: grid;
    place-items: center;
    transition: all 0.3s;

    svg {
    }
    &:hover {
      border: 1px solid $sheer;
    }
  }
}

.picture-label {
  background-color: $dark-blue;
  @extend .absolute-center;
  border-radius: 50%;
  width: 140px;
  height: 140px;
  position: absolute;
  bottom: 0;
  right: 0;
  @include media-breakpoint-down(xs) {
    width: 90px;
    height: 90px;
    img {
      transform: scale(0.7);
    }
  }
  &.red-label {
    background-color: $red;
    top: 0;
    right: 0;
    width: 120px;
    height: 120px;
    @include media-breakpoint-down(xs) {
      width: 80px;
      height: 80px;
    }
  }
}

.drop-icon {
  background-color: $red;
  @extend .absolute-center;
  border-radius: 0 500px 500px 500px;
  position: absolute;
  top: -7%;
  right: 0;
  width: 180px;
  height: 180px;

  @include media-breakpoint-down(xs) {
    width: 90px;
    height: 90px;
    img {
      transform: scale(0.7);
    }
  }
}
