// Theme variables //
$deep-dark-blue: #081D2D;
$dark-blue: #002949;
$red: #F92507;
$white: #ffffff;
$black: #000000;
$gray: #ddd;
$light-gray: #0029490d;
$dark-light: #EFF1F3;
$light: #f7f8f9;
$dark-gray: #333;

$light-sheer: rgba(0, 41, 73, 0.03);

$transition-all: all 0.2s ease-in-out;
$transition-all-slow: all 0.6s ease-in-out;

  //borders

$white-border: 1px solid rgba(255, 255, 255, 0.3);
$white-border-hover: 1px solid rgba(255, 255, 255, 0.6);

$sheer: rgba(255,255,255,0.5);
$extra-sheer: rgba(255,255,255,0.2);


//fonts

$catamaran: 'Catamaran', sans-serif;
$raleway: 'Raleway', sans-serif;
$helvetica: 'helvetica', sans-serif;

// Bootstrap variables //
$grid-breakpoints: (
        xs: 0,
        sm: 576px,
        md: 768px,
        lg: 993px,
        xl: 1280px) !default;

$container-max-widths: (
        sm: 540px,
        md: 820px,
        lg: 960px,
        xl: 1230px
) !default;

$spacer: 1rem !default;
$spacers: () !default;
$spacers: map-merge(
                (
                        0: 0,
                        1: ($spacer * .5),
                        2: ($spacer * .75),
                        3: $spacer,
                        4: ($spacer * 1.5),
                        5: ($spacer * 2),
                        6: ($spacer * 2.5),
                        7: ($spacer * 3)

                ),
                $spacers
);


