.hero-landing {
  padding: 0 15px 100px 15px;
  background-color: $light-sheer;

  .hero-pic {
    height: 436px;
  }

  &.on-top {
    padding: 100px 15px 50px 15px;
  }

  .top-padding {
    padding-top: 80px;
  }

  .hero-tlt {
    position: relative;
    top: -70px;
    color: $white;
  }

  .drop-bg {
  height: 536px;
  width: 489px;
  position: absolute;
  top: 50%;
  right: 50%;
}


  @include media-breakpoint-down(sm) {
    padding: 60px 15px;
    .hero-pic {
      height: 200px;
    }
    .hero-tlt {
      top: -40px;
    }
  }
}

.circle-gradient-red {
  z-index: -1;
  height: 85%;
  width: 85%;
  margin: 20px;
  bottom: 0;
  left: 65%;
  transform: translate(-50% , 30%);
  background: radial-gradient(circle closest-side, rgba(249,37,7,0.2), transparent);
}

.circle-gradient-grey {
  z-index: -2;
  height: 75%;
  width: 75%;
  margin: 20px;
  bottom: 0;
  left: 90%;
  transform: translate(-50% , 30%);
  background: radial-gradient(circle closest-side, rgba(27,33,74,0.2), transparent);
}