#map {
  width: 100%;
  height: 100%;
}

.map-container {
  top: 0;
  bottom: 0;
  width: 628px;
  position: absolute;
  left: 0;
  overflow: hidden;
  @media (max-width: 1339px) {
    width: 568px;
  }
  @include media-breakpoint-down(lg){
    position: relative;
    top: unset;
    bottom: unset;
    left: unset;
    height: 600px;
    max-width: 58%;
  }
  @include media-breakpoint-down(md){
    max-width: unset;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    height: 500px;
    margin-bottom: 30px;
  }
  @include media-breakpoint-down(xs) {
    height: 340px;
  }

}

.map-section {
  // height: 600px;
  background-color: $light-sheer;
  margin-top: 50px;
  margin-bottom: 50px;
  @include media-breakpoint-down(lg){
    height: unset;
  }
  @include media-breakpoint-down(xs){
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .section-title {
    font-size: 18px;
  }
}

a[href^="http://maps.google.com/maps"]{display:none !important}
a[href^="https://maps.google.com/maps"]{display:none !important}

.gmnoprint a, .gmnoprint span, .gm-style-cc {
  display:none;
}
.gmnoprint div {
  //background:none !important;
}

.gm-fullscreen-control {
  display: none;
}

.gmnoprint.gm-bundled-control {
  top: 0;
  bototm: unset;
  .gmnoprint {
    top: 0!important;

    & > div {
      border-radius: 4px!important;
      overflow: hidden;
      width: 32px!important;
      height: 64px!important;
      button {
        width: 32px!important;
        height: 32px!important;
        position: relative;
        &:focus, &:focus-within {
          outline: none;
        }
        img {
          display: none;
        }
        &:after {
          content: "";
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          width: 100%;
          height: 100%;
          background-repeat: no-repeat;
          background-image: url("../../assets/img/plus.svg");
          background-position: center;
        }

        &:nth-of-type(2){
          &:after {
            background-image: none;
            background-color: $dark-blue;
            width: 12px;
            height: 1px;
          }
        }
      }
      & > div {
        width: 32px!important;
        margin: 0!important;
      }
    }
  }
}