.th-gradient {
  background: #F92204;
  background: -webkit-linear-gradient(to right, #F92204 0%, #002949 100%);
  background: -moz-linear-gradient(to right, #F92204 0%, #002949 100%);
  background: linear-gradient(to right, #F92204 0%, #002949 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.dark-blue {
  color: $dark-blue;
}

.red {
  color: $red;
}

.catamaran {
  font-family: $catamaran;
}

.raleway {
  font-family: $raleway;
}

.logo-text {
  font-family: $catamaran;
  font-size: 24px;
  line-height: 39px;
  font-weight: 800;
}

.nav-link {
  font-size: 14px;
  line-height: 16px;
}

.title-xl {
  font-size: 50px;
  line-height: 59px;
  @include media-breakpoint-down(xs) {

  }
}

.title-lg {
  font-size: 40px;
  line-height: 47px;
  font-weight: 700;
  @include media-breakpoint-down(xs) {
    font-size: 30px;
    line-height: 37px;
  }
}

.title-vsm {
  font-size: 16px;
  line-height: 19px;
  font-weight: 700;
  font-family: $helvetica;
  color: $dark-blue;
}
.title-md {
  font-size: 24px;
  line-height: 29px;
  font-weight: 700;
}

.title-sm {
  font-size: 20px;
  line-height: 24px;
  font-weight: 700;
  transition: $transition-all;
  @include media-breakpoint-down(xs) {
    font-size: 16px;
    line-height: 20px;
  }
}

.text {
  // font-size: 16px;
  // line-height: 26px;
  // font-weight: 300;
  font-size: 18px;
  line-height: 28px;
  font-weight: 400;
  @include media-breakpoint-down(xs) {
    font-size: 14px;
    line-height: 22px;
  }
}

.text-reg {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  @include media-breakpoint-down(xs) {
    font-size: 14px;
    line-height: 21px;
  }
}

.text-narrow {
  @extend .text;
  line-height: 18px;
}

.text-sm {
  font-size: 14px;
  line-height: 16px;
}

.footer-title {
  font-size: 16px;
  line-height: 19px;
  font-weight: 700;
  color: rgba(255,255,255,0.2);
  font-family: $raleway;
}

.footer-item {
  @extend .text-sm;
}