header {
  //position: fixed;
  margin: 0 auto;
  position: absolute;
  top: 30px;
  z-index: 400;
  overflow: hidden;
  background-color: transparent;
  left: 20px;
  right: 20px;
  .navbar {
    padding: 0;
  }
  &.scrolled {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    -webkit-box-shadow: 0px 4px 16px 0px rgba(8, 29, 45, 0.38);
    box-shadow: 0px 4px 16px 0px rgba(8, 29, 45, 0.38);

    width: unset;
    & ~ main {
      padding-top: 1px;
    }
    .navbar {
      padding: 0 18px;
      border-radius: 0;
      background-color: $white;
      &.landing-nav {
        background-color: $white;
      }
    }
  }
  .button {
    &.transparent-button {
      border-color: $gray;
      color: $dark-blue;
      &:hover {
        border-color: $gray;
      }
    }
  }
  .logo-white {
    display: none;
  }
  @include media-breakpoint-down(sm) {
    top: 0;
  }
}
nav.navbar {
  height: 80px;

  a:not(.button) {
    transition: color 0.3s;
    &:hover,
    .web-nav-dropdown-toggle:hover {
    }
  }
  .web-nav-dropdown-toggle:hover {
  }
  .navigation-links-wrap {
    .nav-link {
      padding: 0;
      @include media-breakpoint-down(lg) {
        padding: 17px 10px;
        &:before {
          content: none;
        }
      }
    }
    @include media-breakpoint-down(lg) {
      margin-top: 80px;
    }
  }
  .logo {
    z-index: 12;
    @include media-breakpoint-down(xs) {
      img {
        width: 40px;
      }
      .logo-text {
        font-size: 20px;
      }
    }
  }

  &.landing-nav {
    background-color: transparent;
    * {
      color: $dark-blue;
    }
    .navbar-collapse {
      @include media-breakpoint-down(lg) {
        background-color: $red;
        .nav-link {
          border-top: $white-border;
        }
      }
    }
    .hover-for-links {
      &::before {
        background-color: $white;
      }
    }
    .sheer-wrap {
      background-color: $light-gray;
      &:hover {
        background-color: $light-gray;
      }
    }
    .logo-white {
      display: none;
    }
    .logo-red {
      display: inline-block;
    }
    button label {
      span {
        background-color: $white;
      }
    }
  }
}

.navbar-toggler {
  z-index: 12;
  border: none;
  outline: none !important;
  padding: 0;
}

button label {
  position: relative;
  height: 18px;
  width: 29px;
  z-index: 5;

  span {
    position: absolute;
    width: 100%;
    height: 1px;
    top: 50%;
    margin-top: -1px;
    left: 0;
    display: block;
    transition: 0.5s;
    background-color: $dark-blue;
  }

  span:first-child {
    top: 1px;
  }

  span:last-child {
    top: 18px;
  }
}

button:not(.collapsed) {
  label {
    span {
      opacity: 0;
      top: 50%;
    }

    span:first-child {
      opacity: 1;
      transform: rotate(405deg);
    }

    span:last-child {
      opacity: 1;
      transform: rotate(-405deg);
    }
  }
}
@include media-breakpoint-down(lg) {
  .navbar-collapse {
    position: fixed;
    width: 50vw;
    top: 0;
    left: auto;
    overflow-y: auto;
    height: 100vh;
    right: -120%;
    display: block !important;
    transition: 0.3s;
    z-index: 11;
    background-color: $dark-light;
    -webkit-box-shadow: -11px 0px 20px -5px rgba(8, 29, 45, 0.36);
    box-shadow: -11px 0px 20px -5px rgba(8, 29, 45, 0.36);

    @include media-breakpoint-down(sm) {
      width: 100vw;
    }

    &.show {
      right: 0;
    }

    .nav-link {
      padding: 17px 0;
      border-top: 1px solid $gray;
    }

    .nav-buttons {
      border-top: $white-border;
    }
  }
}
.nav-tabs {
  border-bottom: 0;
  .nav-item {
    margin-bottom: 0;
    margin-left: 10px;
    .nav-link {
      border: 1px solid $dark-light;
      border-radius: 10px;
      padding: 25px;
      color: #333;
      font-family: $helvetica;
      text-align: left;
      height: 100%;
      &.active {
        border: 1.5px solid $red;
      }
    }
    &:first-child {
      margin-left: 0;
    }
  }
  &.flex-column {
    .nav-item {
      margin-left: 0;
      margin-bottom: 10px;
    }
  }
  &.tab-col-3 {
    .nav-item {
      width: 30%;
    }
  }
}

.custom-list {
  .list-group-item {
    border-color: $dark-light;
    background-color: transparent;
    color: $dark-blue;
    &.active {
      .text {
        font-weight: bold;
      }
      .btn-round {
        border-color: $red;
      }
    }
    &:last-child {
      border-bottom-width: 1px;
    }
  }
}
