.search-wrap {
  border-radius: 10px;
  background-color: rgba(44, 60, 78, 0.06);
  padding: 20px;
  @include media-breakpoint-down(md){
    max-width: 500px;
  }
  input {
    border: none;
    background-color: transparent;
    outline: none;
    font-size: 16px;
    font-weight: 300;
    line-height: 19px;
    width: 100%;

  }
}

::placeholder {
  opacity: 1;
  color: $black;
}