.partner-wrap {
  border: 1px solid rgba(0, 41, 73, 0.1);
  border-radius: 20px;
  position: relative;
  overflow: hidden;
  @extend .absolute-center;
  width: 100%;
  height: 200px;
  @include media-breakpoint-down(xs){
    height: 160px;
  }
  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(255,255,255,0.1);
    opacity: 1;
    transition: $transition-all;
  }
  &:hover {
    &::after {
      opacity: 0;
    }
  }
}