footer {
  background-color: $deep-dark-blue;
  padding-bottom: 26px;
  padding-top: 60px;
  @include media-breakpoint-down(xs){
    padding-top: 40px;
  }
  * {
    color: $white;
  }
  .logo {
    width: 64px;
    height: 44px;
  }

  .stipriausi-lietuvoje {
    width: 227px;
    height: 60px;
    * {
      stroke: $white;
      fill: $white!important;
    }
  }
  .row:first-of-type {
    padding-bottom: 80px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    @include media-breakpoint-down(xs){
      padding-bottom: 40px;
    }
  }

  .footer-item.hover-for-links {
    &:before {
      bottom: -2px;
    }
  }
  .title-md {
    margin-bottom: 7px;
  }
}