.absolute-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.white {
  color: $white;
}

.white-bg {
  background-color: $white;
}

.mt-tn {
  margin-top: 5px;
}

.mb-tn {
  margin-bottom: 5px;
}

.mr-tn {
  margin-right: 5px;
}

.ml-tn {
  margin-left: 5px;
}

.font-bold, strong {
  font-weight: 700;
}

.font-extra-bold {
  font-weight: 800;
}

.semi-bold {
  font-weight: 600;
}

.font-medium {
  font-weight: 500;
}

.font-regular {
  font-weight: 400;
}

.font-light {
  font-weight: 300;
}



.sheer-wrap {
  background-color: $dark-light;
  border-radius: 50%;
  width: 42px;
  height: 42px;
  @extend .absolute-center;
  &:hover {
    background-color: $dark-light;
  }
}

.w-fit {
  width: fit-content;
}

.gap-bottom-40 {
  gap: 40px 0;
}
.gap-bottom-20 {
  gap: 20px 0;
}

.row-margin-sm {
  margin-left: -10px;
  margin-right: -10px;
}
.row-sp-5 {
  margin-left: -5px;
  margin-right: -5px;
  .col-sp-5 {
    padding-left: 5px;
    padding-right: 5px;
  }
}
.background-filter {
  position: absolute;
  width: 100%;
  background-color: $dark-blue;
  z-index: 9;
  opacity: 0;
  transition: all 0.3s ease-in-out;
  top: 0;
  bottom: 0;
  height: 0;


  &.visible {
    opacity: 0.9;
    transition: all 0.3s ease-in-out;
    height: 200%;
  }
}

.text-xs {
  font-size: 11px;
  line-height: 13px;
  font-weight: 300;
}

.dev-border {
  border: 2px solid darkcyan;
}

.map-icon-wrap {
  @extend .absolute-center;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 1px solid $white;
  background-color: $red;
}
.text-md {
  font-size: 22px;
  line-height: 34px;
}