.container-lg {
  max-width: 1400px;
  padding: 34px 30px 50px 30px;
  border-radius: 20px;
  margin: 0 auto;
  overflow: hidden;
  @include media-breakpoint-down(sm){
    border-radius: 0;
    padding: 34px 18px 50px;
  }
}

.section-container {
  padding-top: 50px;
  padding-bottom: 50px;
  @include media-breakpoint-down(xs){
    padding-top: 30px;
    //padding-bottom: 30px;
  }
}

.container-fluid {
  padding: 0 20px;
  @include media-breakpoint-down(sm){
    padding: 0;
  }
}
