.accordion {
    .accordion-wrap {
        border-radius: 10px;
        margin-bottom: 10px;
        cursor: pointer;
        .accordion-label {
            img {
                transition: all 0.2s ease-in-out;
                transform: rotate(0);
            }
            &:not(.collapsed) {
            img {
                transition: all 0.2s ease-in-out;
                transform: rotate(90deg);
            }
            color: $red;
            transition: all 0.2s ease-in-out;
            }
        }
        &:last-child {
            margin-bottom: 0;
        }
        &:hover {
            .accordion-label {
                color: $red;
                transition: all 0.2s ease-in-out;
            }
        }
    }
}